window.addEventListener(
	"scroll",
	() => {
		document.body.style.setProperty(
			"--scroll",
			window.pageYOffset / (document.body.offsetHeight - window.innerHeight)
		);
	},
	false
);
function swtlanEN() {
	document.getElementById("scrth").style.visibility = "hidden";
	document.getElementById("scrth").style.display = "none";
	document.getElementById("scren").style.visibility = "visible";
	document.getElementById("scren").style.display = "table";
	document.getElementById("button-text1").style.backgroundColor = "#8884AF";
	document.getElementById("button-text2").style.backgroundColor = "#685D88";
}

